html {
    font-size: 10px;
    font-family: sans-serif;
}

p {
    font-size: 1.6rem;
    line-height: 1.5;
}

@font-face {
    font-family: 'billabong';
    src: url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.eot?#iefix") format('embedded-opentype'), url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.woff") format('woff'), url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.ttf") format('truetype'), url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.svg#billabongregular") format('svg');
    font-weight: normal;
    font-style: normal;
}

h1 {
    font-family: billabong, 'billabongregular';
    text-align: center;
    font-weight: 100;
    font-size: 13rem;
    margin: 2rem 0;
    letter-spacing: -1px;
    color: black;
    text-decoration: none;
}

.photoGrid {
    display: flex;
    flex-wrap: wrap;
    max-width: 1000px;
    margin: 0 auto;
   }

.figure {
flex-basis: calc(33.333% - 4rem);
border: 1px solid #d3d3d3;
padding: 2rem;
flex-grow: 1;
margin: 0 2rem 2rem 2rem;
}

.photo {
    width: calc(100% + 4rem);
    margin-left: -2rem;
    margin-top: -2rem;
    max-width: none;
   }

.button-container {
margin: 0 auto;
display: flex;
align-items: center;
justify-content: center;
}

button, .button, .remove-button {
border: 2px solid #f2f2f2;
background: none;
flex-basis: 48%;
display: inline-block;
line-height: 2;
text-decoration: none;
padding: 5px;
text-align: center;
font-size: 15px;
color: red;
transition: all 0.2s;
box-sizing: padding-box;
}